<template>
    <div id="prisonItemInfo">
        <el-form :label-position="labelPosition" label-width="122px" :model="form" :rules="rules" ref="form" class="demo-form">
            <el-row>
                <el-form-item label="" prop="nodeType">
                    <el-select v-model="form.nodeType">
                        <el-option v-for="item in nodeTypes" :key="item.sCode" :label="item.sName" :value="item.sCode"></el-option>
                    </el-select>
                </el-form-item>
            </el-row>
            <el-row>
                <el-form-item label="" prop="nodeName">
                    <el-input style="width: 220px;" v-model="form.nodeName" clearable></el-input>
                </el-form-item>
            </el-row>
            <el-row v-if="form.nodeType == '02'">
                <el-form-item label="" prop="priName">
                    <el-input style="width: 220px;" v-model="form.priName" clearable></el-input>
                </el-form-item>
            </el-row>
            <el-row v-if="form.nodeType == '04'">
                <el-form-item label="" prop="areaType">
                    <el-select v-model="form.areaType">
                        <el-option v-for="item in areaTypes" :key="item.sCode" :label="item.sName" :value="item.sCode"></el-option>
                    </el-select>
                </el-form-item>
            </el-row>
            <el-row>
                <el-form-item label="" prop="nodeState">
                    <el-checkbox v-model="form.nodeState"></el-checkbox>
                </el-form-item>
            </el-row>
            <el-row>
                <el-col :span="16" :offset="8">
                    <el-form-item>
                        <el-button size="small" @click="submitForm('form')" type="primary"></el-button>
                        <el-button size="small" @click="resetForm('form')"></el-button>
                    </el-form-item>
                </el-col>
            </el-row>
        </el-form>
    </div>
</template>

<script>
export default {
    data() {
      return {
          isAddNode: true,          // 
          labelPosition: "right",   // 
          areaTypes: [],            // 
          nodeTypes: [],            // 
          parentNode: {},           // 
          arealList: [],            // 
          form: {                   // 
            nodeId: "",             // 
            nodeName: "",           // 
            orgNodeName: "",        // 
            nodeState: true,          // 
            areaType: "",           // 
            nodeType: "",           // 
            priName: "",            // 
            areas: []               // 
          },
          rules: {                  // 
            nodeName: [{ required: true, message: '', trigger: 'change' }],
            nodeState: [{ required: true, message: '', trigger: 'blur' }],
            nodeType: [{ required: true, message: '', trigger: 'blur' }]
        }
      }
    },
    methods: {
        /**  */
        initAddDatas: function(checkedNode) {
            this.isAddNode = true;
            this.parentNode = checkedNode;

            this.getAreaType();
            this.setNodeDatas();
        },
        /**  */
        initModifyDatas: function(checkedNode) {
            this.isAddNode = false;
            this.parentNode = checkedNode;

            this.getAreaType();
            this.setNodeDatas();
            this.getTreeNodeInfo();
        },
        /**  */
        getAreaType: function() {
            this.$get(this.urlconfig.pmGetAreaTypes).then((res) => {
                if (res.status === 0) {
                    this.areaTypes = res.data;
                }
            }).catch((error) => {
                console.log(error);
            }).then(() => {
                // todo somthing...
            });
        },
        /**  */
        setNodeDatas: function() {
            // 
            let nodeType = this.parentNode.nodeType;
            if (this.isAddNode === true) {
                if (nodeType == "01") { // 
                    this.nodeTypes = [];
                    this.nodeTypes.push({ "sCode" : "02", "sName" : "" });
                } else if (nodeType == "02") { // 
                    this.nodeTypes = [];
                    this.nodeTypes.push({ "sCode" : "03", "sName" : "" });
                } else if (nodeType == "03") { // 
                    this.nodeTypes = [];
                    this.nodeTypes.push({ "sCode" : "03", "sName" : "" });
                    this.nodeTypes.push({ "sCode" : "04", "sName" : "" });
                }
            }
            // 
            else {
                if (nodeType == "02") { // 
                    this.nodeTypes = [];
                    this.nodeTypes.push({ "sCode" : "02", "sName" : "" });
                } else if (nodeType == "03") {  // 
                    this.nodeTypes = [];
                    this.nodeTypes.push({ "sCode" : "03", "sName" : "" });
                } else if (nodeType == "04") {  // 
                    this.nodeTypes = [];
                    this.nodeTypes.push({ "sCode" : "04", "sName" : "" });
                }
            }
            
            if (this.nodeTypes.length > 0) {
                this.form.nodeType = this.nodeTypes[0].sCode;
            }
            this.getAreasByParent();
        },
        /**  */
        getAreasByParent: function() {
            let data = { "isAddNode" : this.isAddNode, "id" : this.parentNode.id, "nodeType" : this.parentNode.nodeType };
            this.$post(this.urlconfig.pmGetAreasByParent, data).then((res) => {
                if (res.status === 0) {
                    this.arealList = res.data;
                }
            }).catch((error) => {
                console.log(error);
            }).then(() => {
                // todo somthing...
            });
        },
        /**  */
        getTreeNodeInfo: function() {
            let data = {"id" : this.parentNode.id, "nodeType" : this.parentNode.nodeType};
            this.$post(this.urlconfig.pmGetTreeNodeInfo, data).then((res) => {
                if (res.status === 0) {
                    this.form = res.data;
                }
            }).catch((error) => {
                console.log(error);
            }).then(() => {
                // todo somthing...
            });
        },
        /**  */
        submitForm: function(formName) {
            this.$refs[formName].validate((valid) => {
                if (valid) {
                    let data = { 
                        "isAddNode": this.isAddNode,
                        "pNodeId" : this.parentNode.id,
                        "pNodeType": this.parentNode.nodeType,
                        "beanItem": JSON.stringify(this.form) 
                    };

                    this.$post(this.urlconfig.pmSaveTreeNode, data).then((res) => {
                        if (res.status === 0) {
                            this.$message.success(res.statusinfo);
                            this.saveNodeCallBack(this.isAddNode, res.data, this.parentNode);
                        }
                    }).catch((error) => {
                        console.log(error);
                    }).then(() => {
                        // todo something...
                    });
                } else {
                    console.log("");
                    return false;
                }
            });
        },
        /**  */
        resetForm: function(formName) {
            this.$refs[formName].resetFields();
        }
    },
    props: [ 'saveNodeCallBack' ]
}
</script>

<style scoped>

</style>


